import { Controller, Commands } from '@ali/4ever-cangjie';
import { getCurrentItem, mergeListData } from '../utils';

export default (editor: Controller) => {
  const { value } = editor;
  const currentList = getCurrentItem(editor);
  const data = currentList[0]?.data;
  if (!data || !data.list || !currentList[0]) return editor;
  return editor.command(
    Commands.setNodeByKey,
    currentList[0].key,
    mergeListData(data, { hideSymbol: true }),
  );
};
